.form-flex{
	.form-group{
		display: flex;
		> label{
			width: 158px;
			min-width: 158px;
			padding-right: 16px;
			margin: 0;
			line-height: $input-height-base;
		}
		&-sm{
			> label{
				line-height: $input-height-small;
			}
			.checkbox{
				margin-top: 5px;
				margin-bottom: 5px;
			}
		}
		&:last-of-type{
			margin-bottom: 0;
		}
	}
	
	select.form-control{
		width: auto;
	}
}

select.form-control{
	padding-right: 24px!important;
}

.radio{
	&-label{
		display: flex;
		flex: 1;
		.price{
			margin-left: auto;
			font-weight: $font-weight-medium;
		}
	}
}

.range-slider-container{
	margin-bottom: 24px;
}

.social-login{
	margin-top: 24px;
}