
.main{
	&-header{
		padding: 36px 0;
		background: $brand-secondary;
		margin-bottom: 0;
		&-content{
			display: flex;
			align-items: center;
		}
		h1 {
			margin: 0;
			color: #fff;
			font-weight: $font-weight-light;
		}
		ul{
			margin-left: auto;
			border: 1px solid $border-light-transparent;
			color: #fff;
			font-size: $font-size-small;
			list-style: none;
			padding: 8px 16px;
			margin-bottom: 0;
			min-width: 294px;
			li{
				padding: 0;
				i{
					margin-right: 8px;
				}
			}
		}
	}
	&-body{
		padding-top: 0;
	}
	&-content{
		padding-top: 40px;
		.section-packages{
			padding-top: 20px;
		}
	}
	
}