@function map-deep-get($map, $keys...) 
{
    @each $key in $keys {
        $map: map-get($map, $key);
    }
    @return $map;
}

@function unicode($str) {
    @return unquote("\"") + $str + unquote("\"");
}


/*-------------------------
* Responsive breakpoint
*
---------------------------*/
$breakpoints:(
    'xs': 575px,
    'sm': 767px,
    'md': 991px,
    'lg':1199px,
    'xlg': 1600px
);

@mixin breakpoint($size){
    @media only screen and (max-width: 992px){

    }
}

@mixin media-breakpoint-down($size) {
    @media only screen and (max-width: map_get($breakpoints, $size)){
        @content;
    }
}

@mixin rtl {
    @if $isrtl == true{
        @content;
    }
}

@mixin ltr {
    @if $isrtl == false{
        @content;
    }
}