.nav-tabs{
	padding: 0;
	.nav{
		&-link{
			&:after{
				height: 2px;
			}
			input[type=radio]{
				display: none;
			}
		}
	}
}

.box{
	box-shadow: $panel-box-shadow;
	&-search-domain{
		border-radius: $border-radius-base;
		background: $brand-secondary;
		padding: 24px;
		.search-box{
			max-width: unset;
			padding: 4px;
			.input-group{
				&-icon{
					margin: 0 18px 0 13px;
				}
			}
			.dropdown{
				> a{
					display: flex;
					align-items: center;
					height: map-get($ui-height, "xlg");
					color: $headings-color;
					font-size: $font-size-large;
					font-weight: $font-weight-medium;
					padding: 0 8px;
					i{
						color: $gray-icons;
						margin-left: 8px;
					}
				}
				&-header{
					padding: 0;
					.form-control{
						padding: 6px 16px;
					}
				}
				&-menu-items{
					max-height: 180px;
				}
			}
		}
	}
}

.spotlight-list{
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	flex: 1;
	margin: 20px -4px 0;
	.spotlight{
		display: flex;
		justify-content: space-between;
		padding: 5px 10px;
		border-radius: $border-radius-base;
		color: #fff;
		font-size: $font-size-small;
		border: 1px solid $border-light-transparent;
		margin-bottom: 8px;
		&-col{
			padding: 0 4px;
			transition: opacity $transition-base, visibility $transition-base;
			flex-grow: 1;
			width: 16.66%;
			&:nth-of-type(n+6){
				&:not(:last-of-type){
					display: none
				}
			}
			&:nth-last-child(n+6), 
			&:nth-last-child(n+6) ~ * {
				flex-grow: unset;
			}
		}
		
		&-more{
			border-style: dashed;
			min-width: 82px;
			justify-content: center;
			transition: border $transition-base;
			&:hover{
				border-color: #fff;
			}
		}
		&-label{
			font-weight: $font-weight-bold;
		}
		&-value{
			font-weight: $font-weight-light;
			color: rgba(255,255,255,0.8);
		}
	}
	&.open{
		.spotlight{
			&-col{
				display: block!important;
			}
		}
	}
}


.search-result{
	border: 1px solid map-get($search-result, "border-color");
	border-radius: $border-radius-base;
	margin-top: 16px;
	padding: 16px;
	display: flex;
	align-items: center;
	font-size: map-get($search-result, "font-size");
	&-body{
		display: flex;
		align-items: center;
	}
	&-message{
		color:  map-get($search-result, "message-color-secondary");
		font-weight:  map-get($search-result, "message-font-weight-secondary");
		margin: 0;
		b{
			color:  map-get($search-result, "message-color-primary");
			font-weight:  map-get($search-result, "message-font-weight-primary");
		}
	}
	&-actions{
		margin-left: auto;
		display: flex;
		align-items: center;
	}
	&-icon{
		min-width: 32px;
		width: 32px;
		height: 32px;
		display: flex;
		justify-content: center;
		align-items: center;
		border: 2px solid map-get($search-result, "icon-color");
		color: map-get($search-result, "icon-color");
		border-radius: 100%;
		margin-right: 16px;
	}
	&-price{
		padding: 0 20px;
		color: map-get($search-result, "price-color");
		font-weight:  map-get($search-result, "price-font-weight");
	}
}

.domain-incart{
	display: flex;
	align-items: center;
	&-actions{
		margin-left: auto;
		display: flex;
	}
	&-body{
		display: flex;
		align-items: center;
	}
	&-message{
		margin: 0;
		color: $gray-base;
		font-size: $font-size-medium;
		b{
			color: $gray-darker;
		}
	}
	&-icon{
		margin-right: 16px;
		color: $brand-primary;
	}
}