.panel{
	&-title{
		+ .panel-desc{
			margin-top: 8px;
		}
	}
	&-desc{
		color: $gray-base;
	}
	&-body{
		.panel{
			&-title{
				margin-top: -6px;
				margin-bottom: 16px;
				
			}
		}
		> *:last-child{
			margin-bottom: 0;
		}
	}
	&-heading{
		.check-content{
			@include ltr {
				margin-left: 36px;
			}
			@include rtl {
				margin-right: 36px;
			}
			> span{
				font-size: $font-size-h4;
				line-height: 28px;
			}
		}
	}
	.check{
		&-sign{
			position: absolute;
			top: 0;
			right: 0;
			height: 30px;
			width: 30px;
			overflow: hidden;
			
			justify-content: center;
			align-items: center;
			display: none;
			i{
				position: absolute;
				background: $brand-primary;
				width: 80px;
				height: 30px;
				transform: rotate(45deg);
				top: -21px;
				&:before{
					position: absolute;
					bottom: 3px;
					color: #fff;
					right: 19px;
					transform: rotate(-45deg);
				}
			}
		}
	}
	&.checked{
		.check{
			&-sign{
				display: flex;
			}
		}
	}
	&-check{
		> .check {
			height: calc(100% - 35px);
		}
		.check{
			padding: 15px;
			align-items: center;
			&-input{
				display: none;
			}
			&-price{
				margin-bottom: 0;
				font-weight: $font-weight-medium;
			}
			&-cycle{
				color: $headings-color;
			}
			&-desc{
				font-size: $font-size-small;
				margin-top: 0;
			}
			
		}
		.panel{
			&-footer{
				padding: 5px 15px;
				color: $brand-primary;
				text-align: center;
				font-weight: $font-weight-bold;
				font-size: $font-size-small;
			}
		}
	}
	&-domain-incart{
		.panel{
			&-body{
				padding: 16px 24px;
				+ .panel-body{
					border-top: 1px solid $gray-lighter-4;
				}
			}
		}
	}
	&-package-info{
		display: flex;
		.panel{
			&-body{
				position: relative;
				width: 50%;
				min-width: 50%;
				+ .panel-body{
					&:before{
						content: '';
						position: absolute;
						width: 1px;
						top: 20px;
						bottom: 20px;
						left: 0;
						background:  $gray-lighter-4;
					}
				}
			}
		}
		ul{
			list-style: none;
			margin-top: 10px;
			padding: 0;
			color: $gray-base;
			b{
				color: $headings-color;
			}
		}
	}
	&-addon{
		display: flex;
		.panel-body{
			display: flex;
			flex-direction: column;
		}
		.panel-icon{
			overflow: hidden;
			width: 262px;
			min-width: 262px;
			position: relative;
			img{
				max-width: calc(100% + 0px);
				position: absolute;
				top: 50%;
				margin-top: -131px;
				right: -8px;
			}
		}
		.panel-actions{
			margin-top: auto;
			display: flex;
			align-items: center;
			&-price{
				display: flex;
				flex-direction: column;
				margin-left: auto;
				text-align: right;
				.price{
					font-weight: $font-weight-medium;
					font-size: $font-size-large;
					&-md{
						font-size: $font-size-base;
					}
					&-sm{
						font-size: $font-size-small;
						color: $gray-base;
						font-weight: $font-weight-base;
					}
					+ .price{
						margin-top: -5px;
						margin-bottom: -6px;
					}
				}
			}
		}
	}
}


.panel-group {
    &-condensed {
        & > .panel {
            .panel-heading + .panel-collapse > .panel-body, 
            .panel-heading + .panel-collapse > .list-group{
			   padding-top: 10px;
			   h4{
				   font-weight: $font-weight-base;
			   }
            }
        }
    }
}

.social-login + .alert{
	margin-top: 24px;
}