.order-sidebar{
	&-content{
		position: sticky;
		top: 24px;
	}

}

.order-summary{
	&-actions{
		flex-direction: column;
	}
	.btn-checkout{
		@if $style == "modern"{
			@include button-variant($btn-primary-color, $btn-primary-bg, $btn-primary-border, $btn-primary-hover-color, $btn-primary-hover-bg, $btn-primary-hover-border, $btn-disabled-color, $btn-disabled-bg, $btn-disabled-border);
		}
		@else{
			@include button-variant($btn-info-color, $btn-info-bg, $btn-info-border, $btn-info-hover-color, $btn-info-hover-bg, $btn-info-hover-border, $btn-disabled-color, $btn-disabled-bg, $btn-disabled-border);
		}
	}
	&-bottom{
		padding-top: 20px;
	}
	.total-due-today{
		padding-top: 0;
		&-text{
			margin-bottom: 2px;
		}
	}
	
	.checkbox{
		margin: 16px 0 0;
		font-size: $font-size-small;
		line-height: $line-height-small;
	}
}

.promocode{
	margin-top: 16px;
	> .btn{
		font-size: $font-size-small;
		line-height: $line-height-small;
		opacity: 1;
		&:not(:hover){
			color: $gray-base;
			opacity: 0.64;
		}
	}
	.input-group{
		padding: 8px 16px;
		border-radius: $border-radius-base;
		@if ($style != 'depth' and $style != 'modern') {
			border: $ui-block-border;
		}
		@if ($style == 'depth' or $style == 'modern')  {
			background: #fff;
			box-shadow: $ui-block-shadow;
		}
	}
	.form-control{
		border: 0;
		padding: 0;
		font-size: $font-size-base;
		&[readonly]{
			background: transparent;
		}
	}
}