.alert{
	&-actions{
		float: right;
		margin: -2px 0;
	}
	&-estimate{
		padding-top: 11px;
		padding-bottom: 11px;
		.alert{
			&-icon{
				&:before{
					content: '\e934';
					margin-top: -4px;
				}
			}
			&-actions{
				margin: -4px 0;
				.btn{
					height: 32px;
					border: 1px solid rgba(#056CEA, 0.16);
					font-size: $font-size-small;
				}
			}
		}

	}
}