.order-content{
	&.full-width{
		max-width: 100%!important;
		.section{
			position: relative;
			&:not(.section-packages){
				padding-left: 70px;
				
			}
			&-number{
				width: 40px;
				height: 40px;
				border-radius: 100%;
				border: 1px solid $gray-lighter-4;
				position: absolute;
				top: -5px;
				left: 0;
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}
		.order-summary{
			&-list{
				&:first-of-type{
					padding-top: 0;
				}
			}
			&-bottom{
				.bottom-content{
					display: flex;
				}
				&.is-fixed{
					position: fixed;
					bottom: 0;
					left: 0;
					right: 0;
					background: #fff;
					padding-top: 16px;
					padding-bottom: 16px;
					box-shadow: 0px 6px 24px rgba(19, 26, 44, 0.04);
					.bottom-content{
						margin-right: auto;
						margin-left: auto;
						padding-left:  floor(($container-gutter / 2));
						padding-right: ceil(($container-gutter / 2));
						@media (min-width: $screen-sm-min) {
							width: $container-sm;
						}
						@media (min-width: $screen-md-min) {
							width: $container-md;
						}
						@media (min-width: $screen-lg-min) {
							width: $container-lg;
						}
					}
					#totalDueToday{
						margin-bottom: -6px;
					}
				}
			}
			&-actions{
				flex-direction: row;
				margin-top: 0;
				margin-left: auto;
				align-items: center;
				.btn{
					order: 1;
					margin-left: 48px;
					min-width: 254px;
				}
				.checkbox{
					margin: 0;
					label{
						display: flex;
						align-items: center;
						a{
							margin-left: 4px;
						}
					}
				}
			}
		}
	}
}